@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Condensed&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pragati+Narrow&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fahkwang:wght@200&display=swap');
.App {
  text-align: center;
  //background: rgba(255, 100, 100,1); // orange red 3
  //background: #59AFFF; // light blue 1
  //background: #D1C300; // yellow 2
  background: transparent; // deepblue 4
}
a:visited{
  color:black !important;
}
a{
  color:black !important;
}

.arrowStroke{
  fill:none;
  stroke:#000;
  stroke-linecap:round;
  stroke-miterlimit:10;
}
.hideVideoBtnArrow{
  width:50px;
  position: fixed;
  top: 33px;
  right: 80px;
  z-index: 22;
  animation: jump .5s linear infinite;
}
.videoSourceTitle{
  text-align: center;
  text-decoration-line:overline;
}
@keyframes jump {
  from {transform: translate(2%,0%);}
  to {transform: translate(-2%,0%);}
}
.playIndex{
  position: fixed;
  top: 64px;
  left: 50%;
  transform:translate(-50%,0%);
  width: 50px;
  height: 1px;
  border-bottom: 1px solid rgba(0,0,0,.1);
}
.playIndexInner{
  position: absolute;
  left: 0%;
  width: 50%;
  height:100%;
  border-bottom: 1px solid black;
}
.index{
  // display: none;
  position: fixed;
  top: 32px;
  left: 50%;
  transform:translate(-50%,0%);
  z-index: 25;
  font-family: 'Barlow', sans-serif;
  font-size: 20px;
}
.aboutContainerClose{
  position:fixed;
  width: 100%;
  height: 100%;
  z-index: 30;
}
.btnDiv{
  position: fixed;
  top: 16%;
  right: 21%;
  z-index: 60;
}
.closeBtn{
  position: absolute;
  width: 40px;
  height: 40px;
  right: 0px;
  top: 0px;
  z-index: 60;
  cursor: pointer;
}
.aboutP{
  width: 80%;
  margin-left: 10%;
}
.about{
  position: fixed;
  top: 50%;
  left: 50%;
  transform:translate(-50%,-50%);
  width: 60%;
  height: 70%;
  //border-left:1px solid black;
  //border-right:1px solid black;
  // border-top:1px solid black;
  border:1px solid black;
  z-index: 30;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  box-shadow:
    0 0 20px 20px rgba(89, 175, 255,1),  /* inner white */
    0 0 34px 29px #fff, /* middle magenta */
    0 0 48px 42px rgba(89, 175, 255,1);
  font-family: 'Pragati Narrow', sans-serif;
  font-size: 30px;
  line-height: 42px;
  overflow-y:scroll;
  text-align: left;
  padding:12px;
}
.videoLine{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 200;
  stroke-dashoffset: 0;
  transition: all .3s ease-in-out;
}
.videoLineAni{
  stroke-dashoffset: 200;
}
.videoElli{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  transition: all .3s ease-in-out;
}
.videoElliAni{
  stroke-dashoffset: 0;
}
.audioLine{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 200;
  stroke-dashoffset: 0;
  transition: all .3s ease-in-out;
}
.audioLineAni{
  stroke-dashoffset: 200;
}
.audioElli{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  transition: all .3s ease-in-out;
}
.audioElliAni{
  stroke-dashoffset: 0;
}
.speedLine{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 200;
  stroke-dashoffset: 0;
  transition: all .3s ease-in-out;
}
.speedLineAni{
  stroke-dashoffset: 200;
}
.speedElli{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  transition: all .3s ease-in-out;
}
.speedElliAni{
  stroke-dashoffset: 0;
}

.stopLine{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 200;
  stroke-dashoffset: 0;
  transition: all .3s ease-in-out;
}
.stopLineAni{
  stroke-dashoffset: 200;
}
.stopElli{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  transition: all .3s ease-in-out;
}
.stopElliAni{
  stroke-dashoffset: 0;
}

.prevLine{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 200;
  stroke-dashoffset: 200;
  transition: all .3s ease-in-out;
}
.prevLineAni{
  stroke-dashoffset: 0;
}

.nextLine{
  fill:none;
  stroke:#000;
  stroke-miterlimit:10;
  stroke-dasharray: 200;
  stroke-dashoffset: 200;
  transition: all .3s ease-in-out;
}
.nextLineAni{
  stroke-dashoffset: 400;
}

