  left: auto;
  right: 7%;
  z-index: 25;
  transform:translate(0%,0%);
}

.index{
  // display: none;
  position: fixed;
  bottom: 35%;
  top:auto;
  left: auto;
  right: 7%;
  font-size: 12px;
  transform:translate(0%,0%);
  // transform:translate(-50%,0%);
}

  .btnDiv{
  top: 16%;
  right: 11%;
}
.closeBtn{
  width: 28px;
  height: 28px;
}
.about{
  width: 80%;
  height: 70%;
